.header{
    color:#fff;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}
.header_icons::-webkit-scrollbar{
    display: none;
}
.header_icons{
    width: 80%;
    display: flex;
    gap: 3rem;
    align-items: center;
    text-align: center;
    cursor: pointer;
    overflow-x: scroll;
}
.header_icon{
    display: flex;
    flex-direction: column;
    /* width: 100px; */
}
.header_icon>p{
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left: auto;
    margin-right: auto;
    font-size:small;
}

.header_icon:hover>p {
    display: inline-flex !important;
}

.header_icon > .MuiSvgIcon-root{
    margin-left: auto;
    margin-right: auto;
}

.header>img{
    width: 120px;
    padding: 0px 20px;
    
    /* height: 80px; */
}

.header_icon-active>p{
    display: inline-flex !important;
}

@media only screen and (max-width: 590px) {
    .header{
        flex-direction: column-reverse;
        gap: 20px;
    }

  }