
li{
list-style: none;
}
.navbar{
    width: 100%;
}
.navbar>div{

    display:flex;
    gap:60px;
    align-items: center;
    color: #fff;
    padding:20px 40px 20px 60px ;
    cursor:pointer;
    overflow-x: scroll;
}
.navbar>div::-webkit-scrollbar{
    display: none;
}

.navbar>div>li{
    text-wrap: nowrap;
    text-transform: capitalize;
    opacity: 0.5;
    font-weight: 500;
    transition: transform 50ms ease-in-out;
}
.navbar>div>li:hover{
    opacity:1;
    transform: scale(1.2);
}

@media only screen and (max-width: 590px) {
    .navbar>div{
        flex-direction: column;
        gap: 20px;
    }

  }
