.Video-Card{
    display: flex;
    flex-direction: column;
    /* align-items: center;
    justify-content: center; */
    color: #fff;
    padding:20px;
    transition: transform 100ms ease-in-out;
    width:300px;


}

.Video-Card:hover{
    transform:scale(1.09);
}

.Video-Card>img{
    width: 100%;
}
.Video-Card>p{
    display: none;
}
.Video-Card>p .MuiSvgIcon-root{
    transform: translate(0px, 4px);
}
.Video-Card:hover>p{
    display: inline-block;

}
